import React, { useEffect, useState, useContext } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';

export const CountContext = React.createContext();


function Cart({cart, count, countHandler}) {

    const [cartt, setCartt] = useState([]);
   


    useEffect(() => {
        const arr = Object.assign(cart, []);
        arr.map(item => {
            item[0].qty = 1;
            item[0].total = item[0].price * item[0].qty;
            return item
        })

        const updatedArr = arr.map((itemm) => {
            return itemm[0]
        })

        setCartt(updatedArr)
        console.log(cartt.length)
        countHandler(cartt.length)
        console.log(count)
    }, [cart])


    const removeFromCart = (id) => {
        const filteredItem = cartt.filter(item => item.id !== id)
        setCartt(filteredItem)
    }

    const increment = (id) => {

    /*Got item id and filter from state array*/
        const item = cartt.filter(item => item.id === id)

    /*Effect changes on item clone*/
        // const itemClone = Object.assign(item, []);
        const updatedItemClone = item[0]
        updatedItemClone.qty = updatedItemClone.qty += 1
        updatedItemClone.total = updatedItemClone.price * updatedItemClone.qty

    /*Cloned the state array*/  
        const carttAlt = cartt.map(item => item)

    /*Added updated item to it's original position*/ 
        const index = carttAlt.findIndex(item => item.id === id)
        carttAlt.splice(index, 1, updatedItemClone);

    /*Updated state array*/
        setCartt(carttAlt)
        console.log(carttAlt);
        
    }

    const decrement = (id) => {
        /*Got item id and filter from state array*/
        const item = cartt.filter(item => item.id === id)
        if(item[0].qty === 1) {
            return
        }
    /*Effect changes on item clone*/
        // const itemClone = Object.assign(item, []);
        const updatedItemClone = item[0]
        updatedItemClone.qty = updatedItemClone.qty -= 1
        updatedItemClone.total = updatedItemClone.price * updatedItemClone.qty

    /*Cloned the state array*/  
        const carttAlt = cartt.map(item => item)

    /*Added updated item to it's original position*/ 
        const index = carttAlt.findIndex(item => item.id === id)
        carttAlt.splice(index, 1, updatedItemClone);

    /*Updated state array*/
        setCartt(carttAlt)
        console.log(carttAlt);
    }


     const totalBill = () => {
        let total = 0;
       if (cartt.length > 0) {
            for(let i = 0; i < cartt.length; i++) {
                total += cartt[i].total
            }
        
       }

        return total;
    }


    useEffect( () => {

        totalBill();
    }, [cartt])


    const cartItem = cartt.map(item => {
        
            let ratingArr =  [
                <FontAwesomeIcon size="1x" icon={["fas", "star"]} />,
                <FontAwesomeIcon size="1x" icon={["fas", "star"]} />,
                <FontAwesomeIcon size="1x" icon={["fas", "star"]} />,
                <FontAwesomeIcon size="1x" icon={["fas", "star"]} />,
                <FontAwesomeIcon size="1x" icon={["fas", "star"]} />
            ];
            const rate = Math.floor(item.rating.rate);
                ratingArr.splice(0, rate);
            for (let i = 0; i < rate; i++) {
                ratingArr.push(<FontAwesomeIcon size="1x" icon={["far", "star"]} />)
            }
            const itemRate = ratingArr.map( (item) => item)
            

        return(
            <div className='cart-item-div' key={item.id}>
                <span onClick={ () => removeFromCart(item.id)} className='cart-item-div-first'><FontAwesomeIcon size="2x" icon={["far", "window-close"]} /></span>
                <div className='cart-item-details-div'>
                    <div>
                        <div className='item-image'>
                            <img src={item.image} style={{width: '100px'}} alt='item-image' />
                        </div>
                        <div className='item-prop'>
                            <p style={{fontStyle: 'uppercase'}}>{item.title}</p>
                            <div>
                                {itemRate}
                            </div>
                            <div style={{
                                    color: 'rgba(182,185,190,1)',
                                    display: 'grid',
                                    gridTemplateColumns: 'auto auto',
                                    placeItems: 'center',
                                    justifyContent: 'start',
                                    alignItems: 'center',
                                }}>
                                <p>Excellent Protection</p>
                                <FontAwesomeIcon style={{paddingBottom: '25px',}} size="3x" icon={["fas", "angle-right"]} />
                            </div>
                        </div>
                    </div>
                    <div style={{
                        display: 'grid',
                        gridGap: '25px',
                        gridTemplateColumns: 'auto auto',
                        justifyContent: 'center',
                        alignItems: 'center',

                    }}>
                        <div style={{
                            display: 'grid',
                            gridGap: '10px',
                            gridTemplateColumns: 'auto 1fr auto',
                            placeItems: 'center',
                            justifyContent: 'center',
                            alignItems: 'center',
                        }}
                        >
                            <button style={{
                                fontSize: '1px',
                            }} onClick={ () => decrement(item.id)}><FontAwesomeIcon size="7x" icon={["fas", "minus"]} /></button>
                            <p style={{
                                paddingTop: '15px',
                            }}>{item.qty}</p>
                            <button style={{
                                fontSize: '1px',
                            }} onClick={ () => increment(item.id)}><FontAwesomeIcon size="7x" icon={["fas", "plus"]} /></button>
                        </div>
                        <div style={{
                                padding: '0px 70px 12px 0px',
                                fontSize: '3.1vmin',
                            }}><b className='pricee'>{item.total}$</b></div>
                    </div>
                </div>
            </div>
        )
    })

    return (
        <div className='cart-div'>
            <div className='cart-item'>
                <p ><b>Thol</b>lexy.</p>
                <p style={{fontStyle: '4vmin'}}>Shopping cart</p>
                <div className='cart-item-div-container'>{cartItem}</div>
                <div className='cart-footer'>
                    <div className='cart-back-div'>
                        <div><FontAwesomeIcon size="1x" icon={["fas", "arrow-left"]} /></div>
                        <p><Link to='/catalog'>Back to catalog</Link></p>
                    </div>
                    <div>
                        <p style={{paddingTop: '10px'}}>Total:<b
                        style={{
                            // paddingTop: '10px',
                            letterSpacing: '.2vmin',
                            fontSize: '5vmin'}}>${Math.floor(totalBill())}</b></p>
                    </div>
                </div>
            </div>
            <div className='card-div'>
                <div className='card-header'>
                    <FontAwesomeIcon size="2x" icon={["far", "window-close"]} />
                    <FontAwesomeIcon size="2x" icon={["fas", "user"]} />
                </div>
                <p>Card Details</p>
                <form className='form-grp'>
                    <div>
                        <label>Card Number</label>
                        <input type='password' placeholder='*** *** *** *** ***' />
                    </div>
                    <div className='card-logo'>
                        <FontAwesomeIcon size="2x" icon={["fab", "cc-mastercard"]} />
                    </div>
                    <div>
                        <label>Name on Card</label>
                        <input type='text' />
                    </div>
                    <div className='expiry-div'>
                        <div>
                            <label>Expiry Date</label>
                            <input type='month' />
                        </div>
                        <div>
                            <label>CVV</label>
                            <input type='password' placeholder='***' />
                        </div>
                    </div>
                    <input value='CHECK OUT' type='submit' />
                </form>
            </div>
        </div>
    )
}

export default React.memo(Cart)
