import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import logo from './images/ALAT.png';
import Hamburger from 'hamburger-react';


export default function Header ({count}) {

    const [isOpen, setOpen] = useState(false)
    const [dimensions, setDimensions] = useState({ 
        height: window.innerHeight,
        width: window.innerWidth
      })

    const display = () => {
        
        const mobileNav = document.querySelector('.hamburger-menu');
        const desktopNav = document.querySelector('.nav-item');

        setDimensions({
            height: window.innerHeight,
            width: window.innerWidth
          })
        
    
        if (dimensions.width > 800) {
            mobileNav.style.display = 'none'
            desktopNav.style.display = 'grid'
        } 

        else {
            mobileNav.style.display = 'grid';
            desktopNav.style.display = 'none'
        }
    }



    useEffect( () => {
       
        display()
        console.log(dimensions.width)
       window.addEventListener('resize', display())
       display();

    return () => {
        window.removeEventListener('resize', display())
    }
    },[])


    useEffect( () => {
        const hamItem = document.querySelector('.hamburger-item');

       if(isOpen) {
           hamItem.style.display = 'grid'
           hamItem.style.transform = 'translateX(0)'
       }
       else {
           hamItem.style.display = 'none'
       }
    })

    const handleMobileNav = () => {
        setOpen(!isOpen)
    }

    return(
        <div className='header-div'>
            
            <div className='logo-div'>
                <Link to='/'><img src={logo} alt='logo' /></Link>
            </div>
            <div className='nav-item'>
                <div>
                    <ul>
                        <li><Link to='/catalog'>Catalog</Link></li>
                        <li><Link to='/popular'>Popular</Link></li>
                        <li><Link to='/about'>About</Link></li>
                        <li><Link to='/news'>News</Link></li>
                    </ul>
                </div>
                <div className='header-icon-div'>
                    <div>
                        <Link to='/cart'><FontAwesomeIcon size="2x" icon={["fas", "shopping-bag"]} /></Link>
                        <span className='cart-length'>{count}</span>
                    </div>
                    <FontAwesomeIcon size="2x" icon={["fas", "user"]} />
                </div>
            </div>
            <div className='hamburger-menu' >
                <div className='hamburger'><Hamburger className='hamburger' toggled={isOpen} toggle={setOpen} /></div>
                <div className='hamburger-item'>
                    
                   <div><Link onClick={handleMobileNav} to='/catalog'>Catalog</Link></div>
                   <div><Link onClick={handleMobileNav} to='/popular'>Popular</Link></div>
                   <div><Link onClick={handleMobileNav} to='/about'>About</Link></div>
                   <div><Link onClick={handleMobileNav} to='/news'>News</Link></div>
                   <div className='hamburger-icon'>
                        <Link onClick={handleMobileNav} to='/cart'><FontAwesomeIcon size="2x" icon={["fas", "shopping-bag"]} /></Link>
                        <FontAwesomeIcon size="2x" icon={["fas", "user"]} />
                   </div>

                </div>
            </div>
        </div>
    )
}