import React, { useState, useEffect, useRef } from 'react';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab, faCcMastercard, faFacebookF, faTwitter, faLinkedinIn, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faCheckSquare, faPlus, faAngleRight, faStar, faMinus, faArrowRight, faArrowLeft, faWindowClose, faCoffee, faUser, faBars, faSquare, faMedal, faShoppingBag } from '@fortawesome/free-solid-svg-icons';
import { faWindowClose as reg, faStar as star } from '@fortawesome/free-regular-svg-icons'
import './App.css';
import Header from './components/Header';
import Home from './components/Home';
import Catalog from './components/Catalog';
import Cart from './components/Cart';

library.add(fab, star, faCcMastercard, faAngleRight, faSquare, faStar, faPlus, faMinus, faArrowRight, faArrowLeft, faWindowClose, reg, faMedal, faBars, faShoppingBag, faCheckSquare, faUser, faCoffee, faFacebookF, faTwitter, faLinkedinIn, faInstagram)


function App() {

  const [post, setPost] = useState([]);
  const [cart, setCart] = useState([]);
  const [count, setCount] =useState(0)

  const divRef = useRef(); 
  
  const countHandler = (sharedValue) => {
    setCount(sharedValue)
    console.log(count)
  }


  const addToCart = (e) => {
    const id = Number(e.target.parentNode.id);
    
    const item = post.filter((item) => {
      return item.id === id;
    })

    const filta =  cart.some(cartItem => {
      return cartItem[0].id === item[0].id
    })

      if (!filta) {
        setCart(prevState => [item, ...prevState])
      }
  }


  let imgCarousel = () => {
    const carouselArr = [];
    for(let i = 0; i < post.length; i++) {
      if(i <= 3 || i >= 17) {
        carouselArr.push(post[i])
      }
    }

    return carouselArr
};

console.log(imgCarousel())

const imageGallery = imgCarousel().map( item => {
 return {
   original: item.image,
   thumbnail: item.image,
   thumbnailClass: 'thumbnail-carousel',
   originalClass: 'img-carousel',
   thumbnailHeight: `${'height=500px'}`,
   thumbnailWidth: 'width=100px',
   originalHeight: 'height=100px',
   originalWidth: 'width=100px',

 }
})

const images = [...imageGallery]


let catalog = post.map(item => {
  return(
        <div className='catalog-item' id={item.id} ref={divRef} key={item.id}>
            <img src={item.image}  />
             <div className='caption'>
               <div className='catalog-item-title'>{item.title}</div>
               <div className='catalog-item- price'>${item.price}</div>
              </div>
             <button  onClick={addToCart}>Add To Cart</button>
          </div>
  )
});

useEffect( () => {
  const fetchData = async () => {
      const data = await fetch('https://fakestoreapi.com/products');
      const response = await data.json()

      setPost(prevState => [...prevState, ...response])
  }
  fetchData();

}, [])

  return (
    <Router>
      <div className="full-container">
        <Header count={count}  />
        <Switch>
          <Route path='/' exact>
            <Home imgCarousel={imgCarousel} post={post} images={images} />
          </Route>
          <Route path='/catalog' exact>
            <Catalog catalog={catalog} />
          </Route>
          <Route path='/cart' exact>
            <Cart count={count} countHandler={countHandler} cart={cart} post={post} />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
